.clothes-btn {
    display: block;
    width: 100%;
    background: #f8f8f8;
    padding: 16px 0;
    font-weight: 500;
    font-size: 13px;
    text-align: center;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: rgba($color: #121212, $alpha: 0.8);
    transition: all ease 0.3s;
    margin-top: 20px;
    cursor: pointer;
    outline: none;
    -webkit-tap-highlight-color: transparent;
    &:hover {
        background: #e6e6e6;
        color: #121212;
        border-color: #e6e6e6;
    }
    &:active {
        background: #9c9c9c;
        border-color: #9c9c9c;
        color: #ffffff;
    }
}
